<template>
<v-container>
  <v-row>
    <v-col cols="12">
      <h1 class="display-1 text-center mb-5 font--text">Events Calendar</h1>
      <v-sheet height="64">
        <v-toolbar flat color="white">
          <v-btn color="primary" class="mr-3" rounded dark @click.stop="dialog = true">
            New Event
          </v-btn>
          <v-btn outlined rounded color="primary" class="mr-4" @click="setToday">
            Today
          </v-btn>
          <v-btn color="primary" fab text small @click="prev">
            <v-icon small>mdi-chevron-left</v-icon>
          </v-btn>
          <v-btn color="primary" fab text small @click="next">
            <v-icon small>mdi-chevron-right</v-icon>
          </v-btn>
          <v-toolbar-title>{{ title }}</v-toolbar-title>
          <div class="flex-grow-1"></div>
          <v-menu bottom right>
            <template v-slot:activator="{ on }">
              <v-btn rounded color="primary" outlined v-on="on">
                <span>{{ typeToLabel[type] }}</span>
                <v-icon right>mdi-menu-down</v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item @click="type = 'day'">
                <v-list-item-title>Day</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = 'week'">
                <v-list-item-title>Week</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = 'month'">
                <v-list-item-title>Month</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = '4day'">
                <v-list-item-title>4 days</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-toolbar>
      </v-sheet>

<!--Modal ADD EVENT-->
<v-dialog v-model="dialog" max-width="500">
  <v-card>
    <v-toolbar color="primary" dark>
        <span class="headline white--text">Add Event</span>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-close</v-icon>
        </v-btn>
    </v-toolbar>
    <v-container>
      <v-form @submit.prevent="addEvent" ref="form">
        <v-text-field v-model="name" type="text" label="Event Name" :rules="[required('Name')]"></v-text-field>
        <v-text-field v-model="details" type="text" label="Details" :rules="[required('Details')]"></v-text-field>
        <v-text-field v-model="start" type="date" label="Start" :rules="[required('Start Date')]"></v-text-field>
        <v-text-field v-model="end" type="date" label="End" :rules="[required('End Date')]"></v-text-field>
        <v-select
            v-model="color"
            :rules="[required('Color')]"
            v-bind:items="colorPicker"
            label="Color">
        </v-select>
        <v-btn type="submit" color="primary" class="mr-4" @click.stop="dialog = false">
          create event
        </v-btn>
      </v-form>
    </v-container>
  </v-card>
</v-dialog>
<!--Modal ADD EVENT (dialogDate)-->
<v-dialog v-model="dialogDate" max-width="500">
  <v-card>
  <v-toolbar color="primary" dark>
    <span class="headline white--text">Add Event</span>
    <v-spacer></v-spacer>
    <v-btn icon dark @click="dialogDate = false">
        <v-icon>mdi-close</v-icon>
    </v-btn>
  </v-toolbar>
    <v-container>
      <v-form @submit.prevent="addEvent" ref="form">
        <v-text-field v-model="name" type="text" label="Event Name" :rules="[required('Name')]"></v-text-field>
        <v-text-field v-model="details" type="text" label="Details" :rules="[required('Details')]"></v-text-field>
        <v-text-field v-model="start" type="date" label="Start" :rules="[required('Start Date')]"></v-text-field>
        <v-text-field v-model="end" type="date" label="End" :rules="[required('End Date')]"></v-text-field>
        <v-select
            v-model="color"
            :rules="[required('Color')]"
            v-bind:items="colorPicker"
            label="Color">
        </v-select>
        <v-btn type="submit" color="primary" class="mr-4" @click.stop="dialog = false">
          create event
        </v-btn>
      </v-form>
    </v-container>
  </v-card>
</v-dialog>

<v-sheet height="400" class="mb-4">
  <!-- CALENDAR -->
  <v-calendar
      ref="calendar"
      v-model="focus"
      color="primary"
      :events="events"
      :event-color="getEventColor"
      :event-margin-bottom="3"
      :now="today"
      :type="type"
      @click:event="showEvent"
      @click:more="viewDay"
      @click:date="setDialogDate"
      @change="updateRange">
  </v-calendar>
  <v-menu
    v-model="selectedOpen"
    :close-on-content-click="false"
    :activator="selectedElement"
    full-width
    offset-x>
  <v-card color="grey lighten-4" :width="350" flat>
    <v-toolbar :color="selectedEvent.color" dark>
      <v-btn @click="deleteEvent(selectedEvent.id)" icon>
        <v-icon>mdi-delete</v-icon>
      </v-btn>
      <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
      <div class="flex-grow-1"></div>
    </v-toolbar>

    <v-card-text>
      <form v-if="currentlyEditing !== selectedEvent.id">
        {{ selectedEvent.details }}
      </form>
      <form v-else>
        <textarea-autosize
        v-model="selectedEvent.details"
        type="text"
        style="width: 100%"
        :min-height="100"
        placeholder="add note">
      </textarea-autosize>
    </form>
  </v-card-text>

  <v-card-actions>
    <v-btn text color="secondary" @click="selectedOpen = false">
      close
    </v-btn>
    <v-btn v-if="currentlyEditing !== selectedEvent.id" text @click.prevent="editEvent(selectedEvent)">
      edit
    </v-btn>
    <v-btn text v-else type="submit" @click.prevent="updateEvent(selectedEvent)">
      Save
    </v-btn>
  </v-card-actions>
</v-card>
</v-menu>
</v-sheet>
<!-- Chip Color -->
<v-chip
    class="ma-2"
    color="red"
    dark
    small
  >
</v-chip>
<span class="mr-5">Important</span>
<v-chip
    class="ma-2"
    color="green"
    dark
    small
  >
</v-chip>
<span class="mr-5">Travel</span>
<v-chip
    class="ma-2"
    color="teal"
    dark
    small
  >
</v-chip>
<span class="mr-5">Events</span>
<v-chip
    class="ma-2"
    color="yellow"
    dark
    small
  >
</v-chip>
<span class="mr-5">Fun</span>
<v-chip
    class="ma-2"
    color="orange"
    dark
    small
  >
</v-chip>
<span class="mr-5">Home</span>  
</v-col>
</v-row>
</v-container>
</template>

<script>
import { db } from '@/main'
export default {
  data: () => ({
    today: new Date().toISOString().substr(0, 10),
    focus: new Date().toISOString().substr(0, 10),
    type: 'month',
    typeToLabel: {
      month: 'Month',
      week: 'Week',
      day: 'Day',
      '4day': '4 Days',
    },
    name: null,
    details: null,
    start: null,
    end: null,
    color: [],
    colorPicker: [
      "red",
      "teal",
      "green",
      "orange",
      "yellow"
      ],
    currentlyEditing: null,
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    events: [],
    dialog: false,
    dialogDate: false,
    required(propertyType){
        return v => v && v.length > 0 || `${propertyType} is reqiured.`
    },
  }),
  mounted () {
    this.getEvents()
  },
  computed: {
    title () {
      const { start, end } = this
      if (!start || !end) {
        return ''
      }
      const startMonth = this.monthFormatter(start)
      const endMonth = this.monthFormatter(end)
      const suffixMonth = startMonth === endMonth ? '' : endMonth
      const startYear = start.year
      const endYear = end.year
      const suffixYear = startYear === endYear ? '' : endYear
      const startDay = start.day + this.nth(start.day)
      const endDay = end.day + this.nth(end.day)
      switch (this.type) {
        case 'month':
        return `${startMonth} ${startYear}`
        case 'week':
        case '4day':
        return `${startMonth} ${startDay} ${startYear} - ${suffixMonth} ${endDay} ${suffixYear}`
        case 'day':
        return `${startMonth} ${startDay} ${startYear}`
      }
      return ''
    },
    monthFormatter () {
      return this.$refs.calendar.getFormatter({
        timeZone: 'UTC', month: 'long',
      })
    }
  },
  methods: {
    async addEvent () {
        await db.collection("calEvent").add({
          name: this.name,
          details: this.details,
          start: this.start,
          end: this.end,
          color: this.color
        })
          this.getEvents()
          this.name = '',
          this.details = '',
          this.start = '',
          this.end = '',
          this.color = ''
          this.$refs.form.reset() 
    },
    async getEvents () {
      let snapshot = await db.collection('calEvent').get()
      const events = []
      snapshot.forEach(doc => {
        let appData = doc.data()
        appData.id = doc.id
        events.push(appData)
      })
      this.events = events
    },
    setDialogDate( { date }) {
      this.dialogDate = true
      this.focus = date
    },
    viewDay ({ date }) {
      this.focus = date
      this.type = 'day'
    },
    getEventColor (event) {
      return event.color
    },
    setToday () {
      this.focus = this.today
    },
    prev () {
      this.$refs.calendar.prev()
    },
    next () {
      this.$refs.calendar.next()
    },
    
    editEvent (ev) {
      this.currentlyEditing = ev.id
    },
    async updateEvent (ev) {
      await db.collection('calEvent').doc(this.currentlyEditing).update({
        details: ev.details
      })
      this.selectedOpen = false,
      this.currentlyEditing = null
    },
    async deleteEvent (ev) {
      await db.collection("calEvent").doc(ev).delete()
      this.selectedOpen = false,
      this.getEvents()
    },
    showEvent ({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event
        this.selectedElement = nativeEvent.target
        setTimeout(() => this.selectedOpen = true, 10)
      }
      if (this.selectedOpen) {
        this.selectedOpen = false
        setTimeout(open, 10)
      } else {
        open()
      }
      nativeEvent.stopPropagation()
    },
    updateRange ({ start, end }) {
      this.start = start
      this.end = end
    },
    nth (d) {
      return d > 3 && d < 21
      ? 'th'
      : ['th', 'st', 'nd', 'rd', 'th', 'th', 'th', 'th', 'th', 'th'][d % 10]
    }
  }
}
</script>